
'use server';

import { 
    appendUserToSheet, 
    getAllUsers, 
    type SignupData, 
    type LoginData, 
    updateUserFolderId, 
    getAllHeaderDataFromSheet,
    addSessionToSheet,
    deleteSessionByToken,
} from '../googlesheets';
import { createFolderInDrive } from '../drive';
import { z } from 'zod';
import { headers, cookies } from 'next/headers';
import { randomUUID } from 'crypto';


const AuthResultSchema = z.object({
    success: z.boolean(),
    error: z.string().nullable(),
    user: z.object({
        userName: z.string(),
        email: z.string().optional(),
        databaseId: z.string().optional(),
        folderId: z.string().optional(),
    }).nullable(),
});

type AuthResult = z.infer<typeof AuthResultSchema>;

export async function signUpUser(userData: SignupData): Promise<AuthResult> {
  try {
    const folderId = await createFolderInDrive(`DATGenie_${userData.userName}`);
    await appendUserToSheet({ ...userData, folderId });
    return { success: true, error: null, user: null };
  } catch (e) {
    console.error('Error signing up user:', e);
    const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
    return { success: false, error: `Failed to sign up: ${errorMessage}`, user: null };
  }
}

export async function loginUser(credentials: LoginData): Promise<AuthResult> {
    try {
        const users = await getAllUsers();
        const user = users.find(u => u.userName === credentials.userName && u.pwd === credentials.password);

        if (user) {
            let folderId = user.folderId;
            if (!folderId) {
                console.log(`User ${user.userName} does not have a folderId. Creating one now.`);
                folderId = await createFolderInDrive(`DATGenie_${user.userName}`);
                await updateUserFolderId(user.userName, folderId);
                console.log(`Successfully created and assigned folderId ${folderId} to user ${user.userName}.`);
            }
            const sheetId = process.env.GOOGLE_SHEETS_SHEET_ID;
            if (!sheetId) {
                 return { success: false, error: 'Google Sheet ID is not configured in the environment.', user: null };
            }

            if (user.databaseId) {
                 try {
                    await getAllHeaderDataFromSheet(user.databaseId);
                } catch(sheetError) {
                    console.error(`Failed to access sheet with ID ${user.databaseId}`, sheetError);
                    return { success: false, error: 'Failed to retrieve data from Google Sheet. Check Sheet ID and permissions.', user: null };
                }
            }
            
            const sessionToken = randomUUID();
            const headersList = headers();
            const ipAddress = headersList.get('x-forwarded-for') || 'Unknown';
            const deviceInfo = headersList.get('user-agent') || 'Unknown';

            await addSessionToSheet({
                userName: user.userName,
                sessionToken,
                ipAddress,
                deviceInfo,
            });
            
            cookies().set('sessionToken', sessionToken, {
                httpOnly: true,
                secure: process.env.NODE_ENV === 'production',
                maxAge: 60 * 60 * 24 * 7, // One week
                path: '/',
            });

            return { 
                success: true, 
                error: null, 
                user: { 
                    userName: user.userName, 
                    email: user.emailAddress, 
                    databaseId: user.databaseId, 
                    folderId: folderId,
                } 
            };
        } else {
            return { success: false, error: 'Invalid username or password.', user: null };
        }
    } catch (e) {
        console.error('Error logging in user:', e);
        const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
        return { success: false, error: `Failed to login: ${errorMessage}`, user: null };
    }
}

const LogoutResultSchema = z.object({ success: z.boolean() });
type LogoutResult = z.infer<typeof LogoutResultSchema>;

export async function logoutUser(): Promise<LogoutResult> {
    const sessionToken = cookies().get('sessionToken')?.value;
    if (sessionToken) {
        await deleteSessionByToken(sessionToken);
        cookies().delete('sessionToken');
    }
    return { success: true };
}
